<template>
	<view class="worthBottomListBox" :style="{'margin-top': dataOriginMapPointerTop +'px','justify-content':flexend}">
		<worthBottomListItem v-for="item in showList" :key="item.topicId" :worthBottomListItem="item" @changeHeight="changeHeight($event)" ></worthBottomListItem>
	</view>
</template>
<script>
	import worthBottomListItem from "./worthBottomListItem.vue"
	export default {
		props:{
			worthBottomList:{
				type:Array,
				default(){
					return []
				}
			},
			showDataLength:{
				type:Number,
				default:9
			}
		},
		created() {
			this.originList = new this.localList(this.showDataLength,function(){
				if(!this.counter && this.counter !== 0){
					this.counter = -1
				}
				if(this.data[this.data.length - 1].appItemHeight && this.counter < this.pointer){
					this.counter = this.pointer
					return true
				}
			})
			this.showList = this.originList.showList()
			this.originList.alertor = this._alertor
		},
		data(){
			return {
				showList:[],
				flexend:'flex-end'
			}
		},
		methods:{
			localList(showDataLength,alertor=null){
				const _data = []
				let _pointer = null
				const _showList = []
				let _alertor = null
				const _changePointer = {
					get(){
						return _pointer
					},
					set(val){
						if(Array.isArray(val) && val.length>0){
							this.upData(val)
						}
						if(Number.isInteger(val) && val > -showDataLength && val < showDataLength && val !== 0){
							this.movePointer(val)
						}
					}
				}
				const _changeData ={
					get(){
						let showData = [..._data]
						return showData
					},
					set(val){
						let dataIndex = _pointer
						let dataName = ""
						let dataValue = null
						if(val instanceof Object){
							let {move,name,data} = val
							if(Number.isInteger(move)){
								dataIndex += move
							}
							if(String(name)){
								dataName = name
							}
							dataValue = data
							if(dataName.trim()){
								_data[dataIndex][dataName]=dataValue
							}
						}
					}
				}
				if(typeof alertor === 'function'){
					this.alertor = null
					_alertor = alertor.bind(this)
				}
				this.showDataLength = showDataLength
				this.movePointer = function(val){
					if(typeof val === 'number' && val > -showDataLength && val < showDataLength && val !== 0){
						let startSliceIndex = 0
						let endSliceIndex = 0 
						let minShowDataLength = parseInt(showDataLength*2/3)
						let showListLength = _showList.length
						let dataLength = _data.length
						if(_pointer - val >= 0 && _pointer + showListLength - val <= dataLength){
							startSliceIndex = _pointer - val
							endSliceIndex = startSliceIndex + showDataLength
							_pointer = startSliceIndex
							_showList.splice(0,showListLength,..._data.slice(startSliceIndex,endSliceIndex))
						} else {
							if(showListLength - minShowDataLength > 0){
								if(val>0){
									if(showListLength - minShowDataLength >= val){
										_showList.splice(-val)
									}else {
										_showList.splice(minShowDataLength)
									}
								}
								if(val<0){
									if(showListLength - minShowDataLength >= -val){
										_showList.splice(0,-val)
									}else{
										_showList.splice(0,showListLength - minShowDataLength)
									}
								}
							}
						}
						if(_alertor !== null){
							let isAlertor = _alertor()
							if(isAlertor !== undefined && typeof this.alertor === 'function'){
								this.alertor(isAlertor)
							}
						}
					}
				}
				this.upData = function(val){
					if(Array.isArray(val) && val.length>0){
						let startSliceIndex = 0
						let endSliceIndex = 0 
						let showListLength = 0
						let dataLength = 0
						val.forEach(item=>{
							let pointerCountIndex = _data.push(item) - 1
							_data[pointerCountIndex].pointerCountIndex = pointerCountIndex
						})
						showListLength = _showList.length
						dataLength = _data.length
						if(showListLength < showDataLength){
							if(!showListLength){
								_pointer =  0
								endSliceIndex = dataLength >= showDataLength ? showDataLength:dataLength
							}else {
								startSliceIndex = _pointer + showListLength
								if(_data[startSliceIndex]){
									if(dataLength - startSliceIndex >= showDataLength - showListLength){
										endSliceIndex = startSliceIndex + showDataLength - showListLength
									} else {
										endSliceIndex = dataLength
									}
								}else{
									startSliceIndex = 0
								}
							}
							if(endSliceIndex - startSliceIndex > 0){
								_showList.push(..._data.slice(startSliceIndex,endSliceIndex))
							}
						}
						if(_alertor !== null){
							let isAlertor = _alertor()
							if(isAlertor !== undefined && typeof this.alertor === 'function'){
								this.alertor(isAlertor)
							}
						}
					}
				}
				this.showList = function(){
					return _showList
				}
				Object.defineProperty(this,"pointer",_changePointer)
				Object.defineProperty(this,"data",_changeData)
			},
			changeHeight(e){
				if(e.itemHeight){
					let index = e.index
					if(!this.originList.data[index].appItemHeight){
						this.originList.data[index].appItemHeight = e.itemHeight
						this.$nextTick(()=>{
							if(!index){
								this.originList.data[index].appMarginTop = 0
							} else {
								this.originList.data[index].appMarginTop = 10 +this.originList.data[index-1].appItemHeight + this.originList.data[index-1].appMarginTop
							}
						})
					}
				}
				if(e.topChang){
					if(!this.flexend !== 'flex-end'){
						this.flexend = 'flex-end'
					}
					
					this.$nextTick(()=>{
						this.originList.pointer = -1
					})
				}
				if(e.bottomChang){
					if(this.flexend !== 'flex-start'){
						this.flexend = 'flex-start'
					}
					this.$nextTick(()=>{
						this.originList.pointer = 1
					})
				}
				
			},
			_alertor(){
				let appItemHeight = this.originList.data[this.originList.data.length -1].appItemHeight
				let appMarginTop = this.originList.data[this.originList.data.length -1].appMarginTop
				let top = 10 + appItemHeight + appMarginTop
				this.$emit('changTop',top)
			},
			app(e){
				console.log(this.originList)
				this.originList.pointer = e
				console.log(this.originList.data)
			}
		},
		computed:{
			dataOriginMapPointerTop(){
				let item =  this.showList[0] || {}
				let dataItem = this.originList.data[this.originList.pointer] || {}
				let top = item.appMarginTo || dataItem.appMarginTop || 0
				return top
			},
		},
		watch:{
			worthBottomList(newData){
				// console.log(newData)
				// let data = JSON.parse(JSON.stringify(newData))
				// this.dataOriginMapPointer = newData
				this.originList.pointer = newData
			}
		},
		components:{
			worthBottomListItem
		}
	}
</script>

<style lang="stylus" scoped>
	.worthBottomListBox
		flex-shrink 0
		display flex
		flex-flow column nowrap
</style>
